'use client';

import { useState, useEffect } from 'react';
import { useAuth } from '@/context/AuthContext';
import { useRouter } from 'next/navigation';
import { collection, getDocs, doc, updateDoc, query, orderBy } from 'firebase/firestore';
import { db } from '@/lib/firebase';
import { Search, Shield, Zap, Coins, Edit, Save, X } from 'lucide-react';

interface UserData {
    id: string;
    displayName: string;
    email: string;
    rank: string;
    exp: number;
    coins: number;
    partyId: string | null;
    role?: string;
}

export default function AdminPage() {
    const { user, loading } = useAuth();
    const router = useRouter();
    const [isAdmin, setIsAdmin] = useState(false);
    const [users, setUsers] = useState<UserData[]>([]);
    const [filteredUsers, setFilteredUsers] = useState<UserData[]>([]);
    const [searchTerm, setSearchTerm] = useState('');
    const [dataLoading, setDataLoading] = useState(true);

    // --- 1. Admin Check ---
    useEffect(() => {
        if (!loading) {
            if (!user) {
                router.push('/');
            } else {
                setIsAdmin(true); // Allow all for testing
                fetchUsers();
            }
        }
    }, [user, loading, router]);

    // --- 2. Fetch Users ---
    const fetchUsers = async () => {
        try {
            setDataLoading(true);
            const q = query(collection(db, 'users'), orderBy('exp', 'desc'));
            const querySnapshot = await getDocs(q);
            const userList: UserData[] = [];
            querySnapshot.forEach((doc) => {
                userList.push({ id: doc.id, ...doc.data() } as UserData);
            });
            setUsers(userList);
            setFilteredUsers(userList);
        } catch (error) {
            console.error("Error fetching users:", error);
        } finally {
            setDataLoading(false);
        }
    };

    // --- 3. Filter Users ---
    useEffect(() => {
        const lowerTerm = searchTerm.toLowerCase();
        const filtered = users.filter(u =>
            u.displayName?.toLowerCase().includes(lowerTerm) ||
            (!u.email ? false : u.email.toLowerCase().includes(lowerTerm))
        );
        setFilteredUsers(filtered);
    }, [searchTerm, users]);

    if (loading || !isAdmin) return <div className="min-h-screen flex items-center justify-center text-white">Checking Guild Credentials...</div>;

    return (
        <div className="min-h-screen bg-[#1a1b26] text-white p-6 pb-20 font-poppins">
            <div className="max-w-7xl mx-auto">

                {/* Header */}
                <header className="flex justify-between items-center mb-10">
                    <div>
                        <h1 className="text-3xl font-bold text-transparent bg-clip-text bg-gradient-to-r from-blue-400 to-purple-500">
                            Guild Command Center
                        </h1>
                        <p className="text-gray-400 text-sm">Manage Members, EXP, and Ranks</p>
                    </div>
                    <button
                        onClick={() => router.push('/dashboard')}
                        className="px-4 py-2 bg-gray-700 hover:bg-gray-600 rounded-lg text-sm transition-all"
                    >
                        Back to Dashboard
                    </button>
                </header>

                {/* Search Bar */}
                <div className="mb-8 relative">
                    <Search className="absolute left-4 top-3.5 text-gray-400 w-5 h-5" />
                    <input
                        type="text"
                        placeholder="Search adventurers by name or email..."
                        value={searchTerm}
                        onChange={(e) => setSearchTerm(e.target.value)}
                        className="w-full h-12 bg-[#242636] border border-gray-700 rounded-xl pl-12 pr-4 focus:border-blue-500 focus:ring-1 focus:ring-blue-500 outline-none transition-all placeholder-gray-500"
                    />
                </div>

                {/* Users Table / Grid */}
                {dataLoading ? (
                    <div className="text-center py-20 text-gray-500">Loading Guild Data...</div>
                ) : (
                    <div className="bg-[#242636] rounded-2xl overflow-hidden border border-gray-700 shadow-xl">
                        <div className="grid grid-cols-12 bg-[#2f3246] p-4 text-xs font-bold text-gray-400 uppercase tracking-wider">
                            <div className="col-span-4">Adventurer</div>
                            <div className="col-span-2 text-center">Rank</div>
                            <div className="col-span-2 text-center">EXP</div>
                            <div className="col-span-2 text-center">Coins</div>
                            <div className="col-span-2 text-center">Actions</div>
                        </div>

                        <div className="divide-y divide-gray-700">
                            {filteredUsers.map((u) => (
                                <div key={u.id} className="grid grid-cols-12 p-4 items-center hover:bg-[#2a2d3e] transition-colors">
                                    {/* Name & Email */}
                                    <div className="col-span-4 flex items-center gap-3">
                                        <div className="w-10 h-10 rounded-full bg-gradient-to-br from-blue-500 to-indigo-600 flex items-center justify-center font-bold text-lg">
                                            {u.displayName?.[0]?.toUpperCase() || '?'}
                                        </div>
                                        <div>
                                            <div className="font-bold text-white">{u.displayName}</div>
                                            <div className="text-xs text-gray-500">{u.email}</div>
                                        </div>
                                    </div>

                                    {/* Rank */}
                                    <div className="col-span-2 flex justify-center">
                                        <span className="px-3 py-1 rounded-full text-xs font-bold bg-gray-700 text-gray-300">
                                            {u.rank}
                                        </span>
                                    </div>

                                    {/* EXP */}
                                    <div className="col-span-2 text-center flex items-center justify-center gap-1 text-blue-300 font-mono">
                                        <Zap className="w-3 h-3" /> {u.exp}
                                    </div>

                                    {/* Coins */}
                                    <div className="col-span-2 text-center flex items-center justify-center gap-1 text-yellow-300 font-mono">
                                        <Coins className="w-3 h-3" /> {u.coins.toLocaleString()}
                                    </div>

                                    {/* Actions */}
                                    <div className="col-span-2 flex justify-center gap-2">
                                        <button className="p-2 bg-blue-500/10 text-blue-400 rounded-lg">
                                            <Edit className="w-4 h-4" />
                                        </button>
                                    </div>
                                </div>
                            ))}
                        </div>
                    </div>
                )}
            </div>
        </div>
    );
}
